@import "@/assets/global.scss";



















.main {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 76px;
  background: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button {
  max-width: $max-width;
  margin: 0 auto;
}

.disabled > div > div {
  background-color: #D2D2D2;
  cursor: unset;
}
